import React, { useState, useEffect } from 'react';

import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Icon from '@mui/material/Icon';

const PostsBar = ({ 
  postsList, 
  setCurrentPost, 
  setDisplaySelect, 
  selectPlay, 
  setSelectPlay, 
  stringAnim, 
  setStringAnim,
  setFadeClose,
  setButtonCooldown
}) => {
  
  const handleSelectPost = (post) => {
    setSelectPlay(post.title);
    setButtonCooldown(true);
    setTimeout(() => {
      setTimeout(() => {
        setCurrentPost(post);
        setDisplaySelect('post');
        setSelectPlay(false);
        setStringAnim(false);
        setButtonCooldown(false);
      }, 1500);
      setFadeClose(true);
    }, 2000);
  }

  useEffect(() => {
    if (selectPlay) {
      let stringTitle = selectPlay.split('');
      setStringAnim(stringTitle);
    }
  }, [selectPlay]);

  return (
    <Box
      display='flex'
      className={!stringAnim ? 'animate-content-start posts-container' : 'posts-container'}
      flexDirection='column'
    > 
      <Grid container spacing={0}>
        {postsList.length > 0 ? postsList.map((post, index) => {
          return <Grid key={index} item xs={11} onClick={!selectPlay ? ()=>handleSelectPost(post) : ()=>console.log('')}>
              <div className={!selectPlay ? 'posts-item' : 'posts-item posts-item-fade'}>
                {!stringAnim ? post.title : stringAnim.map((word, index2) => {
                  return <span key={'l' + index2} style={{
                    '--i': `${index2}`, 'display': 'inline-block'
                  }} className='select-play'>{word}</span>
                })}
                <div className='post-arrow-right'>
                  <Icon fontSize='large'>arrow_right</Icon>
                </div>
              </div>
            </Grid>
          })
        : 
          <Grid item xs={12}>
            <Box display='flex' justifyContent='center' style={{paddingTop: '80px'}}>
              <Button size='small' variant='outlined' disabled>Begin by creating a post above</Button>
            </Box>
          </Grid>
        }
      </Grid>
    </Box>
  )
}

export default PostsBar;